@import url(https://fonts.googleapis.com/css?family=K2D|Montserrat);
body {
  background-color: rgb(34, 34, 32);
  background-position: cover;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;
}

.color-nav-link {
	background-color: #E3B924;
}

.color-nav {
	width: 100%;
	margin: 0;
	background: rgba(7, 7, 6, 0.931);
	box-shadow: 1px -.5px 3px .5px rgb(122, 101, 9, 3);
}

.text-nav {
	font-family: 'K2D', sans-serif;
	font-size: 1.2em;
	font-weight: bold;
	color: rgb(250, 248, 248);
}

.mir {
	font-family: 'K2D', sans-serif;
	font-weight: bold;
	color: rgb(250, 248, 248);
	font-size: 1.6em;
	text-shadow:
		-2px 2px 0 #000,
		1px 4px 0 #000;
}

.size {
	height: 4em;
}

.navbar-fixed { z-index: 9999; }

.height-nav-side {
	height: 100vh;
}

.photo-mir-small {
	display: center;
	margin-top: 30px;
	width: 175px;
	height: 170px;
	border-radius: 50%;
}

.text-nav-side {
	font-family: 'K2D', sans-serif;
	font-size: 1.3em;
	/* font-weight: bold; */
	color: rgb(250, 248, 248);
}

@media screen and (max-width: 600px) {
	.text-nav {
		font-size: calc(1.3em - .3vmin);
	}
	.mir {
		font-size: calc(1.7em - .2vmin);
	}
	nav ul a {
		padding: 0px 5px !important;
	}
}

@media screen and (max-width:320px) {
	.mir {
		font-size: 1.3em;
	}
}
.aboutme {
	color: #FFFFFF;
	margin-top: 5em;
	width: 100;
}

.text-body {
	font-family: 'Montserrat', sans-serif;
	font-size: calc(1em + 1vmin); 
	text-align: justify;
}


.photo-mir {
	height: 50vh;
	margin-top: 1.5em;
	border-radius: 3em;
	box-shadow: 10px 10px 5px 3px rgb(7, 7, 6, 0.671);
}

.mir-about {
	text-align: left;
}

.shadow {
	box-shadow: 1px -.5px 3px .5px rgb(7, 7, 6, 0.671);
}


@media screen and (max-width: 774px) {
	.text-body {
		font-size: calc(.9em + 1vmin);
	}
	.photo-mir {
		display: flex;
		height: 35vh;
	}
}

.background-home {
    background: url(/static/media/fondo.51c89902.png);
    background-size: cover;
    background-repeat: no-repeat;
    width: 100;
    height: 100vh;
}
.background-div {
    width: 100%;
    height: 53vh;
    margin-top: 2em;
    border-radius: 2em
}


.photo-home {
    width: 100%;
    height: 50vh;
    margin-top: 2.5em;
    border-radius: 2em;
    justify-content: center;
}

.text-home {
    margin-top: 5em;
	font-family: 'K2D', sans-serif;
	font-size: .9m;
	font-weight: bold;
    color: rgb(250, 248, 248);
	text-shadow:
		-1px 1px 0 #000,
		1px 3px 0 #000;
}

@media screen and (max-width: 1000px) {
    .photo-home {
        margin-top: .5em;
        height: 200px !important;
    }
    .text-home {
        margin-top: .5em;
    }
    .background-div{
        height: 250px;
    }
}

@media screen and (max-width: 1200px) and (min-height: 1366px) {
    .photo-home{
        height: 200px;
    }
    .background-div,.text-home{
        margin-top: 30em;
    }
}

@media screen and (max-height: 466px) {
    .photo-home{
        height: 150px;
    }
    .background-home {
        height: 50vh !important;
    }
}
.projects {
	font-family: 'Montserrat', sans-serif;
	width: 100%;
	height: 50vh;
}
.title-project{
	color: #ffffff;
	text-align: center;
	font-size: 2em;
	margin-top: 3em;
}

.card{
	height: 350px;
	color: #ffffff;
	/* color: black; */
}

.title-projects{
	font-size: 1.9em;
	margin-bottom: 1em;
	color: #ffffff;
}

.c-click-text {
	color: #ffffff !important;
	font-size: .7rem;
	font-style: oblique;
}
.c-title {
	font-size: 1.5em;
	font-weight: bold;
	margin: .5rem;
}

.tec {
	font-weight: bold;
}

.reve-card {
	font-size: 1.2em;
	background-color: rgb(58, 57, 55) !important;
}


@media screen and (max-width: 420px) {
	.projects{
		margin-top: 1em !important;
		height: 150% !important;
	}
}

@media screen and (min-width: 421px) and (max-width: 600px) {
	.projects {
		margin-top: 1em;
		height: 80vh;
	}

	.c-title {
		font-size: 1.2rem;
	}
}

@media screen and (max-width: 991px){
	.projects{
		height: 105% !important;
		
	}
	.card {
		height: 445px;
		/* width: 105%; */
	}
} 

.contact {
	font-family: 'K2D', sans-serif;
  font-size: calc(.8em + 1vmin); 
	text-align: left;
  width: 100;
	margin-top: 10em;

}
.black {
	background-color: #0C0C0B !important;
}

.tex-link-f {
	color: #FFFFFF;
	width: 100%;
}

.icon-footer {
	height: 2rem;
	margin: .5em;
}

.devicon-footer {
	font-size: 2rem;
	margin: .2em;
}

.devicon-footer-t {
	margin: .5em;
}

@media screen and (max-width: 966px){
  .contact {
    margin-top: 12em;
  }
}
.devicon {
	font-size: 4em;
	padding: 0.7rem .75rem !important;
	color: #a3a3a2;
}

.skills {
	margin-top: 5em;
    color: #FFFFFF;
	text-align: center;
	font-family: 'Montserrat', sans-serif;
    font-size: 1.5em;
    width: 100%;
	/* height: 50vh; */
}

.title-skills {
	margin-bottom: 2em;
}
.firebase {
	padding: 1rem 0 0 0 !important;
	width:5em;
	/* height:4em; */
}


@media screen and (max-width: 500px) {
	.devicon {
		font-size: 1.4em;
	}
	.firebase {
		width:4em;
		height:3em;
	}
}
